.jb-color-picker {
  display: block;
  border-radius: 2px;
  height: 32px;
  width: 100%;
  appearance: none;
  -webkit-appearance: none;
}
.jb-color-picker::-webkit-slider-thumb {
  border-radius: 1px;
  -webkit-appearance: none;
  width: 18px;
  height: 36px;
  border: 2px solid white;
  box-shadow: #0004 0 0 6px;
  background: transparent;
  backdrop-filter: blur(4px);
}
.jb-color-picker::-moz-range-thumb {
  border-radius: 1px;
  width: 18px;
  height: 36px;
  border: 2px solid white;
  box-shadow: #0004 0 0 6px;
  background: transparent;
  backdrop-filter: blur(4px);
  appearance: none;
}
